import Vue from 'vue'    //引入Vue
import Router from 'vue-router'  //引入Vue-route
import Login from '@/components/Login'  //引入根目录下的hello.vue组件
import Home from '@/components/Home'
import editRoute from '@/components/editRoute'
import addStop from '@/components/addStop'
import deleteStop from '@/components/deleteStop'
import searchStop from '@/components/searchStop'
import updateStop from '@/components/updateStop'
import addRoute from '@/components/addRoute'
Vue.use(Router);
export default new Router({
  routes: [//配置路由，是一个数组
    {                          //每一个链接都是一个对象
      path: '/',               //链接路径
      name: 'Login',      //路由名称
      component: Login    //对应的组件模板
    },
      {
        path : '/Home',
          name : 'Home',
          component : Home,
          children:[
              {path: '/addStop', name : 'addStop', component :addStop},
              {path : '/deleteStop', name : 'deleteStop', component : deleteStop},
              {path:'/searchStop', name: 'searchStop', component : searchStop},
              {path:'/updateStop', name: 'updateStop', component : updateStop},
              {path : '/editRoute', name : 'editRoute', component : editRoute},
              {path : '/addRoute', name : 'addRoute', component : addRoute},
          ]

      }
  ]
})



